
@import "@material/top-app-bar/mdc-top-app-bar";

.logo {
  height: 55px;
  width: auto;
  align-self: flex-start;
}
.l_ek, .l_vrk {
  display: none;
}
.topappbar {
  background: white;
  color: #000;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px #9e8181f2, 0px 1px 5px 0px rgba(0, 0, 0, 0.12)
}
.c_btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer {
  background: #4056a1;
  bottom: 0;
  width: 100%;
  height: 64px;
  position: relative;
}
.block_one {
  display: flex;
  justify-content: spa;
  flex-direction: column;
  width: 50%;
}
.footer_row {
  display: flex;
  padding: 0.5em;
  justify-content: space-between;
  color: white;
}
// @media only screen and (max-width: 600px) {
//   .logo {
//     height: 51px;
//     width: auto;
//     align-self: flex-start;
//   }
// }

@media only screen and (max-width: 760px) {
  .wraper {
    background: white;
    display: flex;
  }
  .l_ek, .l_vrk {
    display: block;
    width: auto;
    height: 54px;
  }
  .footer_row {
    flex-direction: column-reverse;
  }
  .block_one {
    width: 98%;
  }
  .block_two {
    width: 98%;
    display: flex;
    justify-content: space-between;
  }

}


